@import url('https://fonts.googleapis.com/css2?family=Poppins:wght@300;400;500&display=swap');

*{
    margin: 0;
    padding: 0;
    box-sizing: border-box;
    font-family: 'Poppins';
}

#mainDiv{
    width: 100%;
    height: 100vh;
    background-color: #F6F6F8;
}

#leftSideDiv{
    background-color: white;
    padding: 20px;
    height: 100vh;
    overflow: hidden;
}



#rightSideDiv{
    background-color: #F6F6F8;
    padding: 20px;
}

#sBar{
    z-index: 1;
    border-radius: 20px;
}

.form-control{
    padding-left: 50px;
}
#groupSearch{
    width: 50%;
    margin: auto;
    position: relative;
    z-index: 1;
}

#sBar::placeholder{
    font-size: 12px;
}

#searchIcon{
    position: absolute;
    top: 20%;
    left: 7%;
    z-index: 2;
}

#searchIcon i{
    color: #7d7d7d;
}

#imageSec{
    margin-top: 15%;
    width: 80%;
}

#imageSec img{
    width: 150px;
    height: 150px;
    margin: auto;
    display: block;
    user-select: none;
    pointer-events: none;
}

#Temp{
    width: 80%;
    margin-top: 10%;
}

#Temp h1{
    text-align: center;
}

#feel{
    font-size: 14px;
    font-weight: 300;
}

#Temp p{
    width: 84%;
    text-align: center;
    font-weight: 500;
}

#littleInfo{
    width: 80%;
    margin-top: 5%;
}

#insideLittleInfo{
    width: 47%;
    margin: auto;
}

#insideLittleInfo p, i{
    font-size: 13px;
    font-weight: 400;
}

.iconSpace{
    margin-left: 3%;
}

#locationCard{
    width: 45%;
    height: 80px;
    margin: 5% 0% 0% 18%;
    position: relative;
}

#locationCard img{
    width: 100%;
    height: 100%;
    background-repeat: no-repeat;
    border-radius: 20px;
    opacity: 0.9;
    pointer-events: none;
    user-select: none;
}

#locationCard p{
    position: absolute;
    z-index: 2;
    font-size: 30px;
    font-weight: 500;
    top: 27%;
    left: 27%;
    color: white;
}

#nav{
    display: flex;
    justify-content: space-between;
    position: relative;
}

#navLeft .nav-pills > li > button{
    color: rgb(139, 139, 139);
    background-color: transparent;
    border-bottom: 2px solid #F6F6F8;
    border-radius: 0px;
    margin-left: 20px;
    padding: 5px;
    font-weight: 500;
}

#navLeft .nav-pills > li > button.active{
    color: black;
    border-bottom: 2px solid black;
}

#navRight{
    margin-right: 15%;
}

#navRight > button{
    width: 46px;
    padding: 10px 2px;
    border-radius: 50%;
    text-align: center;
    margin-left: 10px;
    border: none;
    box-shadow: 0px 5px 10px 0px rgba(0, 0, 0, 0.3);
    background-color: white;
    color: black;
}

#navRight .tempActive{
    background-color: black;
    color: white;
}

#navRight > button:active{
    box-shadow: 0px 3px 7px 0px rgba(0, 0, 0, 0.2);

}
#pills-today{
    padding: 30px;
}

#infoCard{
    width: 100%;
}

.cardContainer{
    display: flex;
    justify-content: space-around;
}

#cardSec{
    margin-top: 50px;
}

#infoCard .cardContainer .card{
    width: 33%;
    height: 250px;
    border: none;
    box-shadow: 0px 5px 12px 0px rgba(0, 0, 0, 0.3);
    border-radius: 7px;
    display: flex;
    justify-content: space-between;
    padding: 20px;
}

#infoCard .cardContainer .cardTitles{
    margin-left: 5%;
    margin-top: 5%;
    color: #606060;
    font-size: 15px;
    letter-spacing: 0.5px;
    font-weight: 400px;
}

#infoCard .cardContainer .cardIcons{
    margin-left: 5%;
}

#infoCard .cardContainer  .cardIcons i{
    font-size: 20px;
    color: #4050D2;
}

#infoCard .cardContainer .cardIcons span{
    font-size: 12px;
    font-weight: 400px;
}


#rainResult h1{
    text-align: center;
    font-size: 40px;
    width: 45%;
}

#windResult h1{
    text-align: center;
    font-size: 40px;
    width: 90%;
}

#timeResult{
    height: 60%;
    display: flex;
    flex-direction: column;
    padding: 10px;
    margin-left: 5%;
}

#timeFirst{
    display: flex;
    margin-bottom: 7%;
}

#timeSec{
    display: flex;
}

#timeSec > span{
    margin-right: 10%;
}

#timeFirst > span{
    margin-right: 10%;
}


#timeFirst > span > i{
    font-size: 25px;
    color: #ffbf00;
}

#timeSec > span > i{
    font-size: 25px;
    color: #ffbf00;
}

#visResult h1{
    width: 70%;
    font-size: 40px;
    text-align: center;
}

#visMsg p{
    margin-left: 8%;
}

#leftSideDive #credit{
    background-color: crimson;
}

